@import '../css/variables.scss';

.container {
  width: 100%;
  min-width: 786px;
  height: 100vh;
  .content {
    margin-top: 240px;
    width: 786px;
    height: 407px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    .text_div {
      position: relative;
      h1 {
        font-size: 56px;
        font-family: 'bold';
      }
      h2 {
        color: #a5a5a5;
        font-size: 32px;
        font-family: 'bold';
      }
      span { // 예시보기
        font-family: 'bold';
        font-size: 14px;
        position: absolute;
        bottom: 0px;
        right: 0px;
        width: 81px;
        height: 44px;
        border-radius: 22px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px #c9c9c9 solid;
      }
    }
  }
  .select_div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
    > div:nth-child(1) {
      background-color: #eef2d5;
    }
    > div:nth-child(2) {
      background-color: #c7e0d2;
    }
    > div {
      height: 218px;
      border-radius: 4px;
      padding: 24px;
      h3 {
        font-size: 20px;
        font-family: 'bold';
        display: flex;
        align-items: center;
        span {
          font-size: 16px;
          font-weight: bold;
          margin-left: 10px;
        }
      }
      p {
        color: #595959;
        font-size: 14px;
        margin-top: 10px;
      }
    }

  }
}